.sidebar {
  height: 100vh;
  background-color: #f5f1ea; /* tono nude/beige */
  position: fixed;
  top: 0;
  left: 0;
  transition: width 0.3s ease;
  padding: 20px 10px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  overflow: hidden;
  width: 40px; /* estado cerrado por defecto */
  z-index: 1000;
  
  /* Borde superior e inferior redondeado */
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.sidebar.open {
  left: 0;
  width: 150px;
}

.hamburger-btn {
  background-color: #d9cfc3;
  border: none;
  /* font-size: 1.5rem; */
  font-size: 10px;
  color: #4a3f35;
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.sidebar a {
  color: #4a3f35;
  text-decoration: none;
  font-weight: bold;
  display: block;
  margin-bottom: 10px;
}

.sidebar a:hover {
  text-decoration: underline;
}
